import axios from "axios";
export default {
  methods: {
    saveChanges() {}

  },
  data: () => ({
    dialog: false,
    autoUpdate: true,
    isUpdating: false,
    snack: false,
    snackColor: "",
    snackText: "",
    valid: false,
    name: "",
    users: [],
    title: "",
    group: {
      name: "",
      description: "",
      users: [],
      usersAdmin: [],
      color: "#448AD1",
      textColor: "",
      created_by: ""
    },
    nameRules: [v => !!v || "This field is required", v => v.length >= 8 || "This field is required"]
  }),

  created() {
    let apiURL = `http://localhost:3000/api/group/${this.$route.params.id}`;
    axios.get(apiURL, {
      headers: {
        token: localStorage.getItem("token")
      }
    }).then(res => {
      this.group = res.data.group;
    });
  }

};